.quote {
  .quote-img {
    height: 230px;

    @include media-breakpoint-up(lg) {
      height: auto;
    }
  }

  .quote-icon {
    left: 5px;
    opacity: 0.6;
    position: absolute;
    top: 18px;
    z-index: 0;
  }

  .quote-cite {
    position: relative;
    z-index: 1;
  }
}
